import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
declare var $ : any;
import { Employee } from '../employee';
import { Workplace } from '../workplace';

@Component({
  selector: 'app-map-modal',
  templateUrl: './map-modal.component.html',
  styleUrls: ['./map-modal.component.css']
})
export class MapModalComponent implements OnInit {
  config = {
    fade: true,
    alwaysOn: false,
    neverOn: false,

    // fill
    fill: true,
    fillColor: '#dde8f4',
    fillOpacity: 0.5,

    // stroke
    stroke: true,
    strokeColor: '#0676f4',
    strokeOpacity: 1,
    strokeWidth: 2,

    // shadow:
    shadow: true,
    shadowColor: '#000000',
    shadowOpacity: 0.8,
    shadowRadius: 10
  };
  employee: Employee;
  workplace: Workplace;
  floor: string;
  xCoord: number;
  yCoord: number;
  //coords: number[];
  enabledImg: string;
  clickedCoords: string[] = new Array();

  constructor(public dialogRef: MatDialogRef<MapModalComponent>, @Inject(MAT_DIALOG_DATA) public data: any) { }

  ngOnInit() {
    this.workplace = this.data.workplace;
    this.floor = this.workplace.floor;
    this.xCoord = parseFloat(this.workplace.xCoord);
    this.yCoord = parseFloat(this.workplace.yCoord);
    
    switch (this.floor) {
      case '0':
        this.enabledImg = '0';
        break;
      
      case '1':
        this.enabledImg = '1';
        break;
        
      case '2':
        this.enabledImg = '2';
        break;

      case '3':
        this.enabledImg = '3';
        break;

      case '4':
        this.enabledImg = '4';
        break;
    
      default:
        break;
    }

    var _this=this;
    $(document).ready(function() {
      
      //make image map responsive
      $('img[usemap]').rwdImageMaps();

      var height:number;
      var width:number;
      setTimeout(function(){
        height = $('img').height();
        width = $('img').width();
        console.log(height+"x"+width);
        
        //show map marker from coordinates (if exists)
        if(!isNaN(_this.xCoord) && !isNaN(_this.yCoord)) {
          $('#mapContainer').append(
            $('<div class="marker glyphicon glyphicon-map-marker "></div>').css({
              position: 'absolute',
              top:  (_this.yCoord*height) + 'px',
              left:  (_this.xCoord*width) + 'px',
            })
          );
        }
      }, 200);

      //show the coordinates of the mouse click as percentages of the image
      $('map').click(function(e) {
        var offset = $('#mapContainer').offset();
        _this.xCoord= (e.pageX - offset.left) / width;
        _this.yCoord = (e.pageY - offset.top) / height;
        _this.clickedCoords[0] =_this.xCoord.toString();
        _this.clickedCoords[1] =_this.yCoord.toString();
        console.log(_this.clickedCoords);
      });
    });
  }

  close() {
    this.dialogRef.close();
    //console.log(this.result);
  }

}
