import { Component, OnInit, Inject } from '@angular/core';
import { TokenStorageService } from '../token-storage.service';
import { Employee } from 'src/app/employee';
import { MatDialogRef, MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MapModalComponent } from 'src/app/map-modal/map-modal.component';
import { UpdateModalComponent } from 'src/app/update-modal/update-modal.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { windowCount } from 'rxjs/operators';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  user: any;
  employee: Employee;

  constructor(public dialogRef: MatDialogRef<ProfileComponent>, 
    @Inject(MAT_DIALOG_DATA) public data: any, public dialog: MatDialog, private snackBar: MatSnackBar) { }

  ngOnInit() {
    this.user = this.data.user;
    this.employee = this.user.employee;
  }

  openMap() {
    const dialogRef = this.dialog.open(MapModalComponent, {
      data: {workplace: this.employee.workplace}
    }); 
    dialogRef.afterClosed().subscribe(result => {
      //console.log(result);
    });
  }

  openUpdate() {
    this.dialogRef.close();
    const dialogRef = this.dialog.open(UpdateModalComponent, {
      minWidth: '30vw',
      minHeight: '30vh',
      data: {employee:this.employee, nameDisabled: true}
    }); 
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        window.location.reload();
        this.openSnackBar("Employee successfully updated", "Close");
      }
    });
  }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }

}
