import { Component, OnInit, Inject } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog} from '@angular/material';
import { Employee } from '../employee';
import { EmployeeService } from '../employee.service';
import { MapModalComponent } from '../map-modal/map-modal.component';
import { EmployeePayload } from '../employee-payload';
import { Workplace } from '../workplace';
import { WorkplaceService } from '../workplace.service';

@Component({
  selector: 'app-update-modal',
  templateUrl: './update-modal.component.html',
  styleUrls: ['./update-modal.component.css']
})
export class UpdateModalComponent implements OnInit {

  employeePayload: EmployeePayload;
  toUpdateEmployee: Employee;
  workplaces: Workplace[];
  workplaceMap: Workplace;
  selectedLocation : string;

  updateEmployeeForm: FormGroup;

  constructor(private employeeService: EmployeeService, public dialogRef: MatDialogRef<UpdateModalComponent>, 
    @Inject(MAT_DIALOG_DATA) public data: any, public dialog: MatDialog, private workplaceService: WorkplaceService) { }

  ngOnInit() {
    this.toUpdateEmployee = this.data.employee;
    //this.selectedLocation = this.toUpdateEmployee.workplace.location;
    console.log(this.toUpdateEmployee);
    
    let loc: string;
    if (this.toUpdateEmployee.workplace) {
      loc = this.toUpdateEmployee.workplace.location;
      this.workplaceMap = this.toUpdateEmployee.workplace;
    } else {
      loc = '';
    }
    this.selectedLocation = loc;
    this.updateEmployeeForm = new FormGroup({
      name : new FormControl({value:this.toUpdateEmployee.name,disabled:this.data.nameDisabled} , [Validators.required,] ),  
      flexWorkplace : new FormControl(this.toUpdateEmployee.flexWorkplace),
      location : new FormControl(loc)
    });

    this.workplaceService.getWorkplacesList().subscribe(data => {
      this.workplaces = data;
    });

  }
  
  get name() {
    return this.updateEmployeeForm.get('name');
  }

  get flexWorkplace() {
    return this.updateEmployeeForm.get('flexWorkplace');
  }

  get location() {
    return this.updateEmployeeForm.get('location');
  }

  onFlexWorkplaceChange() {
    this.location.reset();
  }

  setWorkplace(workplace: Workplace) {
    console.log(workplace);
    this.workplaceMap = workplace;
  }

  updateEmployee() {
    this.employeePayload = new EmployeePayload();
    this.employeePayload.employeeId = this.toUpdateEmployee.id;
    this.employeePayload.name = this.name.value;
    this.employeePayload.flexWorkplace = this.flexWorkplace.value;
    this.employeePayload.workplaceLocation = this.location.value;

    console.log(this.employeePayload);


    this.employeeService.updateEmployee(this.toUpdateEmployee.id, this.employeePayload).subscribe(
      data => {
        console.log(data);
        //this.updateEvent.emit();
        //this.initSongsList();
      }, 
      error => console.log(error));
  }

  resetUpdate(){   
    this.updateEmployeeForm.reset();
  }

  openMap() {
    const dialogRef = this.dialog.open(MapModalComponent, {
      data: {workplace: this.workplaceMap}
    }); 
    dialogRef.afterClosed().subscribe(result => {
      console.log(result);
    });
  }

}
