import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialogRef, MatDialog} from '@angular/material';
import { EmployeeService } from '../employee.service';
import { MapModalComponent } from '../map-modal/map-modal.component';
import { EmployeePayload } from '../employee-payload';
import { WorkplaceService } from '../workplace.service';
import { Workplace } from '../workplace';
import { Observable } from 'rxjs';
import { startWith, map } from 'rxjs/operators';

export interface LocationGroup {
  floor:string;
  workplaces:Workplace[];
}

@Component({
  selector: 'app-add-modal',
  templateUrl: './add-modal.component.html',
  styleUrls: ['./add-modal.component.css']
})
export class AddModalComponent implements OnInit {

  employeePayload: EmployeePayload;
  isAdded: boolean;
  workplaces: Workplace[];
  workplaceMap: Workplace;
  selectedLocation: string;
  locationGroups: LocationGroup[] = new Array();
  locationGroupOptions : Observable<LocationGroup[]>;
  locationValid: boolean = false;
  locationExists: boolean = false;
  addEmployeeForm : FormGroup;

  constructor(private employeeService: EmployeeService, private workplaceService: WorkplaceService, public dialogRef: MatDialogRef<AddModalComponent>, public dialog: MatDialog) { }

  ngOnInit() {
    this.initForm();
    this.workplaceService.getWorkplacesList().subscribe(data => {
      this.workplaces = data;
      this.initLocationGroups();

      this.locationGroupOptions = this.locationGroup.valueChanges
      .pipe(
        startWith(''),
        map(value => this.filterGroup(value))
      );
      //console.log(this.locationGroups);
      //console.log(this.locations);
    });
  }

  initForm() {
    this.addEmployeeForm = new FormGroup({
      name : new FormControl('' , [Validators.required] ), 
      flexWorkplace : new FormControl(''),
      //location : new FormControl(''),
      locationGroup: new FormControl('')
    });
  }

  get name() {
    return this.addEmployeeForm.get('name');
  }

  get flexWorkplace() {
    return this.addEmployeeForm.get('flexWorkplace');
  }

  /*
  get location() {
    return this.addEmployeeForm.get('location');
  }*/

  get locationGroup() {
    return this.addEmployeeForm.get('locationGroup');
  }

  onFlexWorkplaceChange() {
    this.locationGroup.reset();
    this.locationGroup.patchValue('');
  }

  isOccupied(workplace: Workplace): boolean {
    return workplace.occupied == true;
  }

  isCompatible(workplace: Workplace): boolean {
    return workplace.flex == this.flexWorkplace.value;
  }

  //only choosing one of the enabled options enables the add button
  setWorkplace(workplace: Workplace) {
    console.log(workplace);
    //choosing N/A
    if (workplace == null) {
      this.locationValid = true;
      this.locationGroup.reset();
      this.locationGroup.patchValue('');
      return;
    }
    //clicking and choosing an enabled option
    if(!this.isOccupied(workplace) && this.isCompatible(workplace)) {
      this.locationValid = true;
      this.locationExists = true;
      this.workplaceMap = workplace;
    }
  }

  initLocationGroups() {
    let groupExists:boolean = false;
    this.workplaces.forEach(workplace => {
      groupExists = false;
      this.locationGroups.forEach(locationGroup => {
        if(workplace.floor == locationGroup.floor) {
          locationGroup.workplaces.push(workplace);
          groupExists = true;
        }
      });
      if (!groupExists) {
        this.locationGroups.push({floor:workplace.floor,workplaces:[workplace]});
      }
    })
  }

  filterFn(list: Workplace[], value: string) {
    return list.filter(workplace => workplace.location.toLowerCase().includes(value.toLowerCase()));
  }

  private filterGroup(value: string): LocationGroup[] {
    //map is only enabled when clicking on one of the options (except N/A), typing will disable map
    this.locationExists = false;

    //after typing and if the input is not empty, add button will be disabled
    //add button is only enabled after clicking on one of the options or if the input is empty
    if (value) {
      this.locationValid = false;
      return this.locationGroups
          .map(locationGroup => ({floor:locationGroup.floor, workplaces:this.filterFn(locationGroup.workplaces,value)}))
          .filter(locationGroup => locationGroup.workplaces.length > 0);
    }

    //leaving the input empty enables the add button (location value is '')
    this.locationValid = true;
    return this.locationGroups;
  }

  addEmployee() {
    this.employeePayload = new EmployeePayload();
    this.employeePayload.name = this.name.value;
    this.employeePayload.flexWorkplace = this.flexWorkplace.value;
    this.employeePayload.workplaceLocation = this.locationGroup.value;
  
    console.log(this.employeePayload);

    this.employeeService.addEmployee(this.employeePayload).subscribe(
      data => {
        console.log(data);
      }, 
      error => console.log(error));
    this.isAdded=true;
  }

  resetAdd(){  
    this.isAdded=false;  
    this.initForm();
  }

  openMap() {
    const dialogRef = this.dialog.open(MapModalComponent, {
      data: {workplace: this.workplaceMap}
    }); 
    dialogRef.afterClosed().subscribe(result => {
      console.log(result);
    });
  }
}
