import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Employee } from './employee';
import { EmployeePayload } from './employee-payload';

@Injectable({
  providedIn: 'root'
})
export class EmployeeService {
  private baseUrl = 'http://localhost:8080/api/emp/'; 

  constructor(private http:HttpClient) { }

  getEmployeesList() : Observable<any> {
    return this.http.get(`${this.baseUrl}` + 'employeesList');
  }

  addEmployee(employeePayload: EmployeePayload): Observable<object> {
    return this.http.post(`${this.baseUrl}` + 'add', employeePayload);
  }

  deleteEmployee(id: number): Observable<any> {
    return this.http.delete(`${this.baseUrl}delete/${id}`, { responseType: 'text' });  
  }

  getEmployee(id: number): Observable<Employee> {
    return this.http.get<Employee>(`${this.baseUrl}employee/${id}`);
  }

  updateEmployee(id: number, employeePayload: EmployeePayload) :Observable<object> {
    return this.http.post(`${this.baseUrl}` + 'update', employeePayload);
  }
}
