import { Component, OnInit } from '@angular/core';
import { MatDialogRef, MatDialog } from '@angular/material/dialog';
import { AuthService } from '../../auth.service';
import { TokenStorageService } from '../../token-storage.service';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-login-modal',
  templateUrl: './login-modal.component.html',
  styleUrls: ['./login-modal.component.css']
})
export class LoginModalComponent implements OnInit {
  loginForm: FormGroup;
  isLoggedIn = false;
  isLoginFailed = false;
  username: string = "";

  constructor(private authService: AuthService, private tokenStorage: TokenStorageService, 
    public router: Router, public dialogRef: MatDialogRef<LoginModalComponent>, public dialog: MatDialog) { }

  ngOnInit() {
    if (this.tokenStorage.getToken()) {
      this.isLoggedIn = true;
      this.username = this.tokenStorage.getUser().username;
    } else {
      
      this.loginForm = new FormGroup({
        username : new FormControl('' , [Validators.required] ), 
        password : new FormControl('' , [Validators.required] )
      }); 
    }
  }

  onSubmit() { 
    let credentials: any = {};
    credentials.username = this.loginForm.get('username').value;
    credentials.password = this.loginForm.get('password').value;
    this.authService.login(credentials).subscribe(
      data => {
        this.tokenStorage.saveToken(data.accessToken);
        this.tokenStorage.saveUser(data.userDetails);

        this.isLoginFailed = false;
        this.isLoggedIn = true;
        this.username = this.tokenStorage.getUser().username;
        this.reloadPage();
      },
      err => {
        this.isLoginFailed = true;
      }
    );
  }

  reloadPage() {
    this.router.navigate(['/list']);
    setTimeout(() =>
    {
      this.close();
    }, 2000);
  }

  close() {
    this.dialogRef.close();
  }

}
